import React from 'react';
import CustomModal from '../components/common/CustomModal'; // CustomModal

/**
 * 
 * QR
 *
 * @param {object} props - 
 * @param {number} props.amount - 
 * @param {number} props.balance - 
 * @param {object} props.storeId - IDorderIditems
 * @param {function} props.onConfirmPayment - 
 * @param {function} props.onCancelPayment - 
 * @param {boolean} props.isLoading - 
 * @param {function} props.setModal - App.js
 */
export default function PaymentConfirmationScreen({
  amount,
  balance,
  storeId,
  onConfirmPayment = () => { console.warn('onConfirmPayment is not provided'); },
  onCancelPayment,
  isLoading,
  setModal
}) {
  // storeIdIDorderId
  const receiverId = storeId?.id || '';
  const storeName = storeId?.name || '';
  const items = storeId?.items || null; // 
  const orderId = storeId?.orderId || null; // : orderId

  // 
  const handleConfirmClick = () => {
    console.log("PaymentConfirmationScreen: ");
    console.log("PaymentConfirmationScreen: onConfirmPayment ");
    //  (App.js)  onConfirmPayment 
    //  storeName App.jsconfirmPayment
    onConfirmPayment(storeName);
  };

  // 
  const handleCancelClick = () => {
    console.log("PaymentConfirmationScreen: ");
    //  (App.js)  onCancelPayment 
    onCancelPayment();
  };

  return (
    <div className="flex flex-col items-center justify-center min-h-screen bg-gray-900 text-white p-4 font-inter">
      <h2 className="text-3xl font-bold mb-6 text-green-400"></h2>

      {/*  */}
      <div className="bg-gray-800 p-6 rounded-lg shadow-xl w-full max-w-sm mb-8 animate-fade-in-up">
        <div className="text-center">
          <p className="text-xl font-semibold mb-2">: {storeName}</p>
          {orderId && <p className="text-sm text-gray-500 mt-2">ID: {orderId}</p>} {/* orderId */}
          <p className="text-sm text-gray-500 mt-2">ID: {receiverId}</p>
        </div>

        <hr className="border-gray-700 my-4" />

        {items && items.length > 0 ? (
          <div>
            <h4 className="text-lg font-bold mb-2 text-gray-300"></h4>
            <ul className="space-y-2">
              {items.map((item, index) => (
                <li key={index} className="flex justify-between text-base">
                  <span className="text-gray-200">{item.name} <span className="text-gray-400">({item.quantity})</span></span>
                  <span className="font-semibold text-white">{item.price.toLocaleString()}</span>
                </li>
              ))}
            </ul>
            <hr className="border-gray-700 my-4" />
          </div>
        ) : (
          <p className="text-xl text-gray-300 mb-2 text-center"></p>
        )}

        <p className="text-4xl font-extrabold text-white text-center">{amount ? amount.toLocaleString() : '0'}</p>
        <p className="text-lg text-gray-300 mt-4 text-center">: {balance ? balance.toLocaleString() : '0'}</p>
      </div>

      {/*  */}
      <div className="flex space-x-4">
        <button
          onClick={handleConfirmClick}
          className="bg-green-600 hover:bg-green-700 text-white font-bold py-3 px-6 rounded-full shadow-lg transition-all duration-300 transform active:scale-95"
          disabled={isLoading} // 
        >
          
        </button>
        <button
          onClick={handleCancelClick}
          className="bg-red-600 hover:bg-red-700 text-white font-bold py-3 px-6 rounded-full shadow-lg transition-all duration-300 transform active:scale-95"
          disabled={isLoading} // 
        >
          
        </button>
      </div>

      {/*  (isLoadingtrue) */}
      {isLoading && (
        <div className="mt-8">
          <p className="text-lg text-gray-400">...</p>
        </div>
      )}
    </div>
  );
}
