// src/screens/QrCodeScanner.js

import React, { useEffect, useRef, forwardRef, useCallback, useImperativeHandle } from 'react';
import jsQR from 'jsqr'; // jsQR

// QrCodeScanner 
// isActive: /
// onResult: QR (QR)
// onError: 
// constraints: MediaDevices.getUserMedia() 
// scanDelay:  ()
// videoContainerStyle: 
// videoStyle: 
const QrCodeScanner = forwardRef(({ isActive, onResult, onError, constraints, scanDelay = 500, videoContainerStyle, videoStyle }, ref) => {
  const videoRef = useRef(null);
  const canvasRef = useRef(null);
  const animationFrameId = useRef(null);
  const streamRef = useRef(null); // MediaStream
  const lastScanTime = useRef(0); // 

  // 
  useImperativeHandle(ref, () => ({
    getCanvas: () => canvasRef.current,
    stopCamera: () => {
      stopCamera();
    },
    startCamera: () => {
      startCamera();
    }
  }));

  // 
  const stopCamera = useCallback(() => {
    console.log("QrCodeScanner: stopCamera - ");
    if (animationFrameId.current) {
      cancelAnimationFrame(animationFrameId.current);
      animationFrameId.current = null;
      console.log("QrCodeScanner: stopCamera - ");
    }
    if (streamRef.current) {
      streamRef.current.getTracks().forEach(track => {
        track.stop();
        console.log(`QrCodeScanner: stopCamera -  ${track.kind} `);
      });
      streamRef.current = null;
    }
    if (videoRef.current) {
      videoRef.current.srcObject = null;
      if (!videoRef.current.paused) { // paused 
        videoRef.current.pause();
        console.log("QrCodeScanner: stopCamera - ");
      }
    }
    console.log("QrCodeScanner: stopCamera - ");
  }, []);

  // QR
  const scanQRCode = useCallback(() => {
    if (!videoRef.current || !canvasRef.current || !videoRef.current.videoWidth || videoRef.current.paused) {
      // 
      animationFrameId.current = requestAnimationFrame(scanQRCode);
      return;
    }

    const video = videoRef.current;
    const canvas = canvasRef.current;
    // : willReadFrequently  true 
    const context = canvas.getContext('2d', { willReadFrequently: true }); 

    // 
    canvas.width = video.videoWidth;
    canvas.height = video.videoHeight;

    // 
    context.drawImage(video, 0, 0, canvas.width, canvas.height);

    // 
    const currentTime = Date.now();
    if (currentTime - lastScanTime.current > scanDelay) {
      lastScanTime.current = currentTime;

      // jsQRQR
      const imageData = context.getImageData(0, 0, canvas.width, canvas.height);
      const code = jsQR(imageData.data, imageData.width, imageData.height, {
        inversionAttempts: "dontInvert", // QR
      });

      if (code) {
        console.log("QrCodeScanner: scanQRCode - QR:", code.data);
        // : onResult 
        onResult({ text: code.data }); 
        // QRisActivefalse
        // stopCamera(); // 
        return; // QR
      } else {
        // console.log("QrCodeScanner: scanQRCode - QR");
        // QR
      }
    }
    
    // 
    animationFrameId.current = requestAnimationFrame(scanQRCode);
  }, [onResult, scanDelay]); // onResult 

  // 
  const startCamera = useCallback(async () => {
    console.log("QrCodeScanner: startCamera - ");
    if (streamRef.current) { // 
      console.log("QrCodeScanner: startCamera - ");
      return;
    }

    try {
      // 
      console.log("QrCodeScanner: startCamera - navigator.mediaDevices.getUserMedia Constraints:", constraints);
      const stream = await navigator.mediaDevices.getUserMedia(constraints);
      streamRef.current = stream; // ref
      if (videoRef.current) {
        videoRef.current.srcObject = stream;
        
        // 
        videoRef.current.onloadedmetadata = () => {
          console.log("QrCodeScanner: startCamera - onloadedmetadata ");
          videoRef.current.play().then(() => {
            console.log("QrCodeScanner: startCamera - ");
            scanQRCode(); // 
          }).catch(playError => {
            console.error("QrCodeScanner: startCamera - :", playError);
            onError(new Error(`: ${playError.message || playError.name || playError.toString()}`));
            stopCamera(); // 
          });
        };
      }
    } catch (err) {
      console.error("QrCodeScanner: startCamera - :", err);
      onError(err); // 
      stopCamera(); // 
    }
  }, [constraints, onError, stopCamera, scanQRCode]); // scanQRCode 

  useEffect(() => {
    console.log(`QrCodeScanner: useEffect - isActive ${isActive} `);
    if (isActive) {
      startCamera();
    } else {
      stopCamera();
    }

    // 
    return () => {
      console.log("QrCodeScanner: useEffect  - ");
      stopCamera(); // 
    };
  }, [isActive, startCamera, stopCamera]);


  return (
    <div style={videoContainerStyle}>
      {/*  */}
      <video ref={videoRef} playsInline muted style={{ display: 'none', ...videoStyle }}></video>
      <canvas ref={canvasRef} style={{ width: '100%', height: '100%', objectFit: 'cover' }}></canvas>
    </div>
  );
});

export default QrCodeScanner;
