// src/utils/logToFirestore.js

import { collection, addDoc, serverTimestamp } from 'firebase/firestore';
import { firebaseDb } from '../firebase/firebaseConfig'; // Firestore

const db = firebaseDb; // Firestore

/**
 * Firebase Firestore
 * 'error'
 * @param {string} appId - ID
 * @param {string} level -  ('log', 'warn', 'error')
 * @param {...any} args - 
 */
export const logToFirestore = async (appId, level, ...args) => {
  // 'error'Firestore
  if (level !== 'error') {
    return; // 'error'
  }

  // Firebase
  if (!db || !appId) {
    console.warn('logToFirestore: FirebaseappId');
    return;
  }

  try {
    // 
    const message = args.map(arg => {
      if (typeof arg === 'object' && arg !== null) {
        try {
          return JSON.stringify(arg);
        } catch (e) {
          return String(arg); // JSON.stringify
        }
      }
      return String(arg);
    }).join(' ');

    // appId
    await addDoc(collection(db, `artifacts/${appId}/dev_logs`), {
      timestamp: serverTimestamp(), // 
      level: level, // 'log', 'warn', 'error'
      message: message,
      userAgent: navigator.userAgent,
      //  (App.js)
      userId: window.firebaseAuth?.currentUser?.uid || 'anonymous',
      currentScreen: window.currentAppScreen || 'unknown',
    });
  } catch (e) {
    // Firestoreconsole.warn
    console.warn('Firestore:', e);
  }
};
