// src/screens/AccountScreen.js

import React, { useState, useEffect, useCallback } from 'react';
import { doc, getDoc, updateDoc, setDoc, serverTimestamp } from 'firebase/firestore'; // getDoc, setDoc, updateDoc, serverTimestamp 
import { signOut, signInAnonymously } from 'firebase/auth'; // signOut, signInAnonymously 
import LoadingSpinner from '../components/common/LoadingSpinner';

// 
import UserProfileSection from './UserProfileSection';
import AuthManagementSection from './AuthManagementSection';
import StoreSettingsSection from './StoreSettingsSection';

const AccountScreen = ({
  profileImage,
  handleProfileImageUpload,
  isStoreMode,
  setIsStoreMode,
  storeLogo,
  handleStoreLogoUpload,
  db,
  userId,
  appId,
  setModal,
  auth,
  setScreen,
  setToast,
  userName, // App.jsuserName
  setUserName // App.jssetUserName
}) => {
  const [userEmail, setUserEmail] = useState(''); // AuthManagementSection
  const [isEmailVerified, setIsEmailVerified] = useState(false); // AuthManagementSection
  const [isAccountLoading, setIsAccountLoading] = useState(true); // AccountScreen
  const [isAnonymousUser, setIsAnonymousUser] = useState(false); // AuthManagementSection

  // Firestore
  const fetchUserProfile = useCallback(async () => {
    if (!db || !userId || !auth || !auth.currentUser) {
      setIsAccountLoading(false);
      return;
    }

    setIsAccountLoading(true);
    setIsAnonymousUser(auth.currentUser.isAnonymous);

    try {
      const userProfileRef = doc(db, `artifacts/${appId}/users/${userId}/profile`, 'userProfile');
      const docSnap = await getDoc(userProfileRef);

      if (docSnap.exists()) {
        const data = docSnap.data();
        setUserName(data.name || ''); // App.jsuserName state
        setUserEmail(auth.currentUser.email || data.email || '');
        if (typeof data.isStore === 'boolean') {
          setIsStoreMode(data.isStore);
        }
      } else {
        //  (App.jsuseAppInit)
        await setDoc(userProfileRef, {
          name: auth.currentUser.displayName || '',
          email: auth.currentUser.email || '',
          balance: 0,
          points: 0,
          profileImageUrl: '',
          phoneNumber: '',
          address: {
            zipCode: '',
            prefecture: '',
            city: '',
            streetAddress: '',
            buildingName: ''
          },
          isStore: false,
          createdAt: serverTimestamp()
        });
        setUserName(auth.currentUser.displayName || '');
        setUserEmail(auth.currentUser.email || '');
        setIsStoreMode(false);
      }
      setIsEmailVerified(auth.currentUser.emailVerified);

    } catch (error) {
      console.error("Error fetching user profile:", error);
      const errorMessage = `\n: ${error.message || error.toString()}`;
      setModal({
        isOpen: true,
        title: '',
        message: errorMessage,
        onConfirm: () => setModal(prev => ({ ...prev, isOpen: false })),
        showCancelButton: false,
      });
    } finally {
      setIsAccountLoading(false);
    }
  }, [db, userId, appId, setModal, auth, setIsStoreMode, setUserName]);

  useEffect(() => {
    fetchUserProfile();
  }, [fetchUserProfile]);

  // 
  const handleLogout = useCallback(async () => {
    setModal({
      isOpen: true,
      title: '',
      message: '\n',
      showCancelButton: true,
      onConfirm: async () => {
        setModal(prev => ({ ...prev, isOpen: false }));
        setIsAccountLoading(true);
        try {
          await signOut(auth);
          await signInAnonymously(auth);
          setScreen('guest_intro');
          setToast({ message: '', type: 'success' });
        } catch (error) {
          console.error("Logout failed:", error);
          setModal({
            isOpen: true,
            title: '',
            message: `\n: ${error.message || error.toString()}`,
            onConfirm: () => setModal(prev => ({ ...prev, isOpen: false })),
            showCancelButton: false,
          });
          setToast({ message: `: ${error.message || ''}`, type: 'error' });
        } finally {
          setIsAccountLoading(false);
        }
      },
      onCancel: () => {
        setModal(prev => ({ ...prev, isOpen: false }));
        setToast({ message: '', type: 'info' });
      }
    });
  }, [auth, setScreen, setModal, setToast]);

  return (
    <div className="p-4 text-white animate-fade-in font-inter">
      <h2 className="text-3xl font-bold mb-6 text-center text-yellow-300"></h2>

      {isAccountLoading ? (
        <LoadingSpinner />
      ) : (
        <>
          {/*  */}
          {isAnonymousUser && (
            <div className="mb-6 p-4 bg-yellow-600 rounded-xl shadow-lg text-center animate-bounce-in">
              <p className="font-bold text-lg mb-2"> </p>
              <p className="text-sm">
                <br/>
                
              </p>
              <button
                onClick={() => setScreen('register')}
                className="mt-4 bg-blue-500 text-white px-6 py-2 rounded-full text-md font-semibold hover:bg-blue-600 transition-all duration-300 transform hover:scale-105"
              >
                
              </button>
            </div>
          )}

          {/*  */}
          <UserProfileSection
            profileImage={profileImage}
            handleProfileImageUpload={handleProfileImageUpload}
            db={db}
            userId={userId}
            appId={appId}
            setModal={setModal}
            setToast={setToast}
            userName={userName}
            setUserName={setUserName} // App.jssetUserName
            isAnonymousUser={isAnonymousUser}
            isEmailVerified={isEmailVerified} // 
            setScreen={setScreen} // 
          />

          {/*  () */}
          {!isAnonymousUser && (
            <AuthManagementSection
              auth={auth}
              db={db}
              userId={userId}
              appId={appId}
              setModal={setModal}
              setToast={setToast}
              userEmail={userEmail}
              setUserEmail={setUserEmail}
              isEmailVerified={isEmailVerified}
              setIsEmailVerified={setIsEmailVerified}
              setScreen={setScreen}
            />
          )}

          {/*  */}
          <StoreSettingsSection
            db={db}
            appId={appId}
            userId={userId}
            setModal={setModal}
            setToast={setToast}
            isStoreMode={isStoreMode}
            setIsStoreMode={setIsStoreMode}
            storeLogo={storeLogo}
            handleStoreLogoUpload={handleStoreLogoUpload}
            isAnonymousUser={isAnonymousUser}
            setScreen={setScreen}
          />

          {/*  */}
          <div className="mt-8 text-center animate-fade-in-up">
            <button
              onClick={handleLogout}
              className="bg-red-600 text-white px-8 py-3 rounded-full text-lg font-semibold shadow-lg hover:bg-red-700 transition-all duration-300 transform hover:scale-105 active:scale-95"
            >
              
            </button>
          </div>
        </>
      )}
    </div>
  );
};

export default AccountScreen;
