// src/components/layout/DebugInfo.js

import React from 'react';

/**
 * 
 * @param {object} props - 
 * @param {string} props.screen - 
 * @param {string|null} props.userId - ID
 * @param {object|null} props.auth - Firebase Auth
 * @param {boolean} props.isFirebaseReady - Firebase
 * @param {boolean} props.isInitialDataLoaded - 
 * @param {boolean} props.splashScreenTimerCompleted - 
 * @param {string} props.scanMode - 
 */
const DebugInfo = ({ screen, userId, auth, isFirebaseReady, isInitialDataLoaded, splashScreenTimerCompleted, scanMode }) => {
  return (
    <div className="mx-4 mb-4 p-3 bg-gray-700 rounded-lg text-white text-sm">
      <p>: <span className="font-bold">{screen}</span></p>
      <p>ID: <span className="font-bold break-all">{userId || ''}</span></p>
      <p>: <span className="font-bold">{auth?.currentUser?.isAnonymous ? '' : ''}</span></p>
      <p>Firebase Ready: <span className="font-bold">{isFirebaseReady ? '' : ''}</span></p>
      <p>Initial Data Loaded: <span className="font-bold">{isInitialDataLoaded ? '' : ''}</span></p>
      <p>: <span className="font-bold">{splashScreenTimerCompleted ? '' : ''}</span></p>
      <p>: <span className="font-bold">{scanMode}</span></p> 
    </div>
  );
};

export default DebugInfo;
